import React from 'react';
import './About.css';
import pic from './images/takeshi3.jpg';
import pic_2 from './images/kaggle.png';
import pic_3 from './images/facebook.png';

function About() {
    return (
        <div className="about_section">
            <div className="max-width">
                <h1>About</h1>   
                
                <div className="about_container">
                    <div className="left">
                        <img src={pic} alt=""/>
                    </div> 
                    <div className="right">
                        <h2 className="about_name">大谷武史</h2>
                        <p className="right-p">愛知県蒲郡市生まれ、38歳</p> 
                        <p className="right-p">2002年 - 2005年 : 石田厨機(株)<br></br>高校卒業後、厨房器具メーカー石田厨機(株)に就職、厨房器具の製造を担当。</p>
                        <p className="right-p">2005年 - 2022年 : フリーランスピアニスト<br></br>ジャズピアニストとして東京で1年、ニューヨークで16年活動する。自身のバンドTakeshi Otani Groupでアメリカのテレビ番組PepC Showに出演、ニューヨークの老舗ジャズクラブ等で演奏する。</p>
                        <p className="right-p">2021年 - : <br></br>データサイエンティストを志しプログラミングの学習始める、データ分析、機械学習システム構築、統計学等をメンターから学ぶ。</p>

                        <div className="contact">
                            <div className="img_div">
                                <a href="https://www.kaggle.com/takeshiotani/code" target="_blank">
                                    <img className="img-1" src={pic_2} alt=""/>
                                </a>
                                <a href="https://www.facebook.com/otani1220" target="_blank">
                                    <img className="img-2" src={pic_3} alt=""/>
                                </a>
                            </div>
                            {/*
                            <h3>Contact me!</h3> 
                            <div className="img_div_2">
                                <p>otani6454@gmail.com</p>
                            </div>
                            */}
                        </div>
                    </div>      
                </div>
            </div>
        </div>
    )
}

export default About
